@import url(https://fonts.googleapis.com/css2?family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Milonga&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Milonga&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Mountains+of+Christmas&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Elsie&family=Milonga&family=Mountains+of+Christmas&display=swap);
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

* {
  border: 0 none;
  list-style: none outside none;
  margin: 0;
  outline: 0 none;
  padding: 0;
}

body {
  padding: 0;
  margin: 0 auto;
  height: 100%;
  width: 100%;
  font-size: 16px;
  line-height: 20px;
  color: #333;
  overflow-x: hidden;
  font-family: Arial, Helvetica, sans-serif;
  position: relative;
}

.mealslist-heading li,
.mealslist-heading select {
  padding-right: 10px;
  width: 59px;
  height: 36px;
  font-family: 'Milonga', cursive;
  font-size: 13px;
  font-weight: normal;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 1.9px;
  color: #121212;
  cursor: pointer;
}

.mealslist-heading select {
  width: 140px;
  font-weight: bold;
  background-color: #d57d1f;
}

.mealslist-heading li:first-child {
  width: 250px;
  height: 37px;
  font-family: 'Mountains of Christmas', cursive;
  font-size: 30px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #008000;
  padding: 2px;
  margin: 0;
}

ul {
  display: flex;
  justify-content: space-between;
  padding-right: 100px;
  padding-left: 60px;
  border-bottom: 1px solid #e8e8e8;
  padding-bottom: 18px;
  z-index: 100000;
  height: 130px;
}

.content {
  background-color: #000;
  padding-top: 45px;
}

.content button {
  width: 160px;
  font-family: 'Elsie', cursive;
  font-size: 13px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: center;
  color: #fff;
  height: 33px;
  margin-right: 190px;
  border-radius: 3px;
  background-color: #2d2013;
  cursor: pointer;
  border: 1px solid #555;
  padding: 3px;
}

@media (max-width: 825px) {
  ul {
    display: block;
  }

  ul li {
    margin-top: 15px;
  }
}

.meal-row {
  height: 200px;
  padding: 32px 119px 26px 27px;
  margin-top: 40px;
  display: flex;
  justify-content: space-evenly;
  margin-right: 100px;
  margin-left: 100px;
  border-radius: 4px;
  border: solid 1px #e8e8e8;
  background-color: #d57d1f;
}

.category {
  width: 49px;
  height: 18px;
  margin: 0 39px 0 0;
  opacity: 0.5;
  font-family: 'Milonga', cursive;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
}

.title {
  width: 197px;
  height: 58px;
  margin: 3px 323px 0 0;
  font-family: 'Elsie', cursive;
  font-size: 22px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.2px;
  color: #121212;
}

.title-category,
.image-area .right {
  width: 250px;
}

.image-area {
  height: 150px;
  width: 150px;
  border: solid 3px #2d2013;
}

a {
  text-decoration: none;
}

.tags {
  margin-left: 53px;
  opacity: 0.5;
  font-family: 'Milonga', cursive;
  font-size: 14px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: normal;
  color: #121212;
}

.meal-row button {
  font-family: 'Elsie', cursive;
  font-size: 13px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: center;
  color: #fff;
  width: 160px;
  height: 33px;
  margin: 12px 0 9px 59px;
  border-radius: 3px;
  background-color: #2d2013;
  border: #2d2013;
  cursor: pointer;
  padding: 1px;
  border-right: 1px solid #8b8b8b;
  border-left: 1px solid #fff;
  border-bottom: 1px solid #fff;
}

.meal-row button:hover {
  border: 2px outset #def;
  color: #121212;
  background-color: #f8f8f8 !important;
}

.hidden {
  visibility: hidden;
}

.highlighted {
  background-color: yellow;
}

@media (max-width: 1250px) {
  .meal-row {
    display: block;
    height: inherit;
    margin-left: 0;
    margin-right: 0;
  }

  .meal-row button,
  .tags {
    margin: 10px 10px 0 0;
  }

  .title-category,
  .title,
  .category {
    width: 100%;
  }
}

@media (min-width: 900px) and (max-width: 1250px) {
  .image-area {
    width: unset;
    height: 600px;
  }
}

@media (min-width: 488px) and (max-width: 900px) {
  .image-area {
    height: 400px;
    width: unset;
  }
}

.no-meals {
  color: #fff;
  font-family: 'Milonga', cursive;
  text-align: center;
  height: 40px;
}

h2 {
  text-align: center;
  font-family: 'Mountains of Christmas', cursive;
  color: #2d2013;
}

.list-by-alphabet {
  background-color: #d57d1f;
  margin: 10%;
  border: 3px solid #2d2013;
  padding: 5%;
}

.list-by-alphabet .ul {
  list-style: none;
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.ul li {
  padding: 8px;
  font-family: 'Elsie', cursive;
}

a {
  text-decoration: underline;
  color: #008000;
}

.image-area.details-pg {
  height: 300px;
  width: 300px;
  margin: 0 auto;
}

.instructions {
  font-family: 'Milonga', cursive;
  padding: 26px;
}

.ins {
  background-color: #d57d1f;
  padding-left: 2%;
  padding-right: 2%;
  height: 400px;
}

.ins button,
.header button {
  font-family: 'Elsie', cursive;
  font-size: 13px;
  font-weight: 300;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: 0.5px;
  text-align: center;
  color: #fff;
  width: 160px;
  height: 33px;
  margin-top: 20px;
  border-radius: 3px;
  background-color: #2d2013;
  border: 1px solid #fff;
  cursor: pointer;
  padding: 1px;
  border-right: 1px solid #8b8b8b;
}

.details-pg {
  height: 142px;
  display: flex;
  background-color: #d57d1f;
  margin-right: 400px;
  margin-left: 400px;
  padding: 32px 119px 26px 27px;
}

.ins-heading {
  display: flex;
  justify-content: space-between;
}

.body-ins {
  font-family: 'Milonga', cursive;
}

.details-bg {
  background-color: #000;
}

.title-categ {
  flex-grow: 1;
  flex-basis: 100%;
}

.ingredients {
  flex-grow: 2;
}

.titl {
  font-family: 'Elsie', cursive;
  font-size: 22px;
  font-weight: bold;
  font-stretch: normal;
  font-style: normal;
  line-height: normal;
  letter-spacing: -0.2px;
  color: #121212;
}

@media (max-width: 1500px) {
  .details-pg {
    height: 100%;
    margin-right: 120px;
    margin-left: 120px;
  }
}

@media (max-width: 800px) {
  .details-pg {
    display: block;
    height: unset;
    padding: 1rem;
  }
}

@media (max-width: 601px) {
  .ins {
    height: unset;
  }
}

@media (max-width: 500px) {
  .details-pg {
    margin-left: 0;
    margin-right: 0;
  }
}

@media (max-width: 400px) {
  .ins button {
    margin: 12px 17px 9px 59px;
    width: 100px;
  }

  .ins {
    padding: 2rem 0;
  }
}

